import React, { useEffect } from 'react'
import { Route, Routes, Navigate } from "react-router-dom";
import Login from '../pages/Auth/login/Login'
import Register from '../pages/Auth/register/Register'
import ResetPassword from '../pages/Auth/resetPassword/ResetPassword'
import ResetConfirm from '../pages/Auth/resetConfirmPassword/ResetConfirm'
import Dashboard from '../pages/Layout/Dashboard'
import UserManagement from '../pages/Layout/usermanagment/UserManagement';
import { AppRoute, AuthRoute } from './AuthGraud';
import { useDispatch } from 'react-redux';
import { login, logout } from '../redux/features/authSlice';
import { appapi } from '../api/apiClient';

export default function RootNavigation() {

    const dispatch = useDispatch()

    useEffect(() => {
        const value = localStorage.getItem('diz');
        if (value) {
            const userData = JSON.parse(value)
            dispatch(login(userData))
        }
    }, [dispatch])

    useEffect(() => {
        const tokenMonitor = (response) => {
            const { ok, status } = response;
            if (!ok && status === 403) {
                dispatch(logout())
            }
        }

        appapi.axiosInstance.interceptors.request.use(
            (config) => {
                // console.log(config);
                const data = JSON.parse(localStorage?.getItem('diz')) || {};
                const token = data?.accessToken
                if (token) {
                    config.headers.Authorization = `Bearer ${token}`;
                }
                return config;
            },
            (error) => {
                Promise.reject(error);
            },
        );

        appapi.addMonitor(tokenMonitor);
    }, []);

    /**
     * Route has been Protected based on user Logged In or not.
     */
    return (
        <Routes>
            <Route path='/login' element={<AppRoute><Login /></AppRoute>} />
            <Route path='/forgotPassword' element={<AppRoute><ResetPassword /></AppRoute>} />
            <Route path='/register/:verificationToken/:emailId' element={<AppRoute><Register /></AppRoute>} />
            <Route path='/createPassword/:verificationToken' element={<AppRoute><ResetConfirm /></AppRoute>} />
            <Route path='/' element={<AuthRoute><Dashboard /></AuthRoute>} >
                <Route index element={<Navigate to="userManagement" replace />} />
                <Route path='userManagement' element={<UserManagement />} />
                <Route path='analytics' element={<div className='main-Container'>Analytics</div>} />
                <Route path='payments' element={<div className='main-Container'>Payments</div>} />
                <Route path='ecoInitiatives' element={<div className='main-Container'>EcoInitiatives</div>} />
                <Route path='integrations' element={<div className='main-Container'>Integrations</div>} />
                <Route path='settings' element={<div className='main-Container'>Setting</div>} />
            </Route>
            <Route
                path="*"
                element={<Navigate to="/" replace />}
            />
        </Routes>
    )
}
