import '../UserManagement.scss';
import React, { useState } from 'react';
import { Dialog, Slide, Stack, Typography, DialogActions, Button, IconButton, TextField, InputAdornment, MenuItem, useMediaQuery } from '@mui/material';
import ClearIcon from '@mui/icons-material/Clear';
import PersonIcon from '@mui/icons-material/Person';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import GroupOutlinedIcon from '@mui/icons-material/GroupOutlined';
import * as yup from 'yup';
import { FieldArray, getIn, Formik, Form } from 'formik';
import { buttonStyle3, dialogButtonStyle, dialogButtonCancel } from '../../../../utils/commonStyle';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';
import { useSelector } from 'react-redux';
import { appapi } from '../../../../api/apiClient';
import { showmessage } from '../../../../utils/toastr';

//Transtion of dialog from downwards to up
const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

yup.addMethod(yup.array, "unique", function (message, mapper = (a) => a) {
    return this.test("unique", message, function (list) {
        return list.length === new Set(list.map(mapper)).size
    })
})

export default function AddUserDialog({ open, enterpriseUser, onHandleAddClose }) {

    const { rolesList } = useSelector(state => state.app);

    const [inviteCheck, setInviteCheck] = useState(false);
    const [inputArr, setInputArr] = useState([
        {
            emailId: '',
            rolePermissionId: '',
            createdBy: 'kumar'
        },
    ])

    const inviteUser = async (obj) => {
        obj.userArr.map((item) => {
            let find = rolesList.find((role) => role.roleId === item.rolePermissionId)
            if (find) {
                item[`roleName`] = find.roleName
            }
        })
        setInputArr(obj.userArr)
        setInviteCheck(true);
    }

    const handleSendInvitation = async () => {
        inputArr.map((item) => {
            delete item.roleName
        })
        let params = {
            params: {
                enterpriseId: enterpriseUser.enterpriseId
            }
        }
        let payload = inputArr
        const response = await appapi.post(`/user/${enterpriseUser.enterpriseId}`, payload, params)
        if (response.ok) {
            const data = response.data;
            handleCloseDialog()
            if (data.status === 'success') {
                showmessage(data.message)
            } else {
                showmessage(data.message, 5000, 'error')
            }

        } else {
            showmessage(`Something Went Wrong!`, 5000, 'error')
        }
    }

    const isMobile = useMediaQuery('(max-width:1000px)');

    const IconContainer = ({ children, backgroundColor }) => {
        return (
            <div className='iconBox flex-center' style={{ background: backgroundColor, borderRadius: '4px', width: '32px', height: '32px' }}>
                {children}
            </div>
        )
    }

    const handleCloseDialog = () => {
        setInputArr([
            {
                emailId: '',
                rolePermissionId: '',
                createdBy: 'kumar'
            },
        ])
        setInviteCheck(false);
        onHandleAddClose()
    }

    /**
    * dialog style
    */
    const sx = {
        "& .MuiDialog-container": {
            alignItems: "end",
        },
        "& .MuiPaper-root": {
            width: '712px',
            maxHeight: isMobile ? '620px' : '552px',
            margin: isMobile ? '0' : '8px',
            padding: '0 16px 23px 16px',
            borderRadius: isMobile ? '16px 16px 0 0' : '4px',
            maxWidth: '712px',
            overflow: 'hidden'
        }
    };

    return (
        <Dialog
            sx={sx}
            open={open}
            onClose={handleCloseDialog}
            TransitionComponent={Transition}
            className='AddUserContainer'
        >
            <Stack style={{ borderBottom: '1px solid rgb(112,112,112,0.3)', paddingBottom: '10px', marginBottom: '16px', position: 'sticky', top: '0', paddingTop: '23px', zIndex: '99', background: '#FFF' }} direction='row' justifyContent='space-between' alignItems='center' >
                <div className='flex'>
                    {inviteCheck ? '' : <PersonIcon style={{ color: '#8AB756' }} />}
                    <Typography style={{ color: '#445C37', marginLeft: '16px' }} className='fw-bold fs-14 ff-Seigo' >
                        {inviteCheck ? 'CONFIRMATION' : 'ADD USER'}
                    </Typography>
                </div>
                <div>
                    <IconButton onClick={handleCloseDialog}>
                        <ClearIcon style={{ color: '#445C37', transform: 'scale(1.143)' }} />
                    </IconButton>
                </div>
            </Stack>
            {
                inviteCheck ?
                    <Stack>
                        <Stack style={{ height: isMobile ? '412px' : '350px', overflow: 'scroll' }}>
                            {inputArr.map((item) => (
                                <Stack>
                                    <Stack direction={isMobile ? 'column' : 'row'} justifyContent='space-between' style={{ borderBottom: '1px solid rgb(112,112,112,0.3)', padding: '16px 0', rowGap: isMobile ? '16px' : '' }}>
                                        <Stack direction='row' style={{ width: '50%' }}>
                                            <IconContainer backgroundColor='#445C37'>
                                                <MailOutlineIcon style={{ color: '#E7EEDE', transform: 'scale(0.8)' }} />
                                            </IconContainer>
                                            <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
                                                <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Email</Typography>
                                                <Typography style={{ color: '#474747' }} className='ff-Seigo fs-14 fw-regular'>{item?.emailId}</Typography>
                                            </Stack>
                                        </Stack>
                                        <Stack direction='row' style={{ width: '50%' }}>
                                            <IconContainer backgroundColor='#445C37'>
                                                <GroupOutlinedIcon style={{ color: '#E7EEDE', transform: 'scale(0.8)' }} />
                                            </IconContainer>
                                            <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
                                                <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Roles</Typography>
                                                <Typography style={{ color: '#474747' }} className='ff-Seigo fs-14 fw-regular'>{item?.roleName || '-'}</Typography>
                                            </Stack>
                                        </Stack>
                                    </Stack>
                                </Stack>
                            ))
                            }
                        </Stack>
                        <DialogActions style={{
                            height: '66px',
                            justifyContent: 'flex-end',
                            padding: '0',
                            marginTop: '20px'
                        }}>
                            <Button sx={dialogButtonCancel} variant='outlined' onClick={() => setInviteCheck(false)}>Cancel</Button>
                            <Button sx={dialogButtonStyle} style={{ marginLeft: '16px' }} onClick={handleSendInvitation} variant="contained">Send</Button>
                        </DialogActions>
                    </Stack>
                    :
                    <Formik
                        initialValues={{
                            userArr: inputArr
                        }}
                        validationSchema={
                            yup.object().shape({
                                userArr: yup.array().of(
                                    yup.object().shape({
                                        emailId: yup
                                            .string('Enter your Email')
                                            .matches(/^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/, 'Email is not Valid')
                                            .required('Email is Required'),
                                        rolePermissionId: yup
                                            .string('Select your role')
                                            .required('Role is Required'),
                                    })
                                ).unique("Duplicate emails not allowed", (a) => a.emailId),
                            })
                        }
                        onSubmit={values =>
                            inviteUser(values)
                        }
                    >
                        {({ values, touched, errors, handleChange, handleBlur, isValid }) => (
                            <Form noValidate autoComplete="off">
                                <FieldArray name="userArr">
                                    {({ push, remove }) => (
                                        <>
                                            <div style={{ height: isMobile ? '412px' : '350px', overflow: 'scroll' }}>
                                                {values.userArr.map((p, index) => {
                                                    const emailId = `userArr[${index}].emailId`;
                                                    const touchedEmailId = getIn(touched, emailId);
                                                    const errorEmailId = getIn(errors, emailId);

                                                    const rolePermissionId = `userArr[${index}].rolePermissionId`;
                                                    const touchedrolePermissionId = getIn(touched, rolePermissionId);
                                                    const errorRolePermissionId = getIn(errors, rolePermissionId);

                                                    return (
                                                        <Stack key={index} className='editContainer addContainer'>
                                                            <Stack style={{ background: '#F2F2F2', padding: '8px', marginBottom: '8px', position: 'relative' }}>
                                                                <div style={{ textAlign: 'end', position: 'absolute', top: '0', right: '0' }}>
                                                                    {
                                                                        values.userArr.length > 1
                                                                        &&
                                                                        <IconButton style={{
                                                                            height: '32px',
                                                                            width: '32px'
                                                                        }} onClick={() => remove(index)}>
                                                                            <RemoveIcon />
                                                                        </IconButton>
                                                                    }
                                                                </div>
                                                                <Stack direction={isMobile ? 'column' : 'row'} justifyContent='space-between' style={{ rowGap: isMobile ? '14px' : 'initial' }}>
                                                                    <Stack style={{ width: isMobile ? '100%' : '328px' }}>
                                                                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Email</Typography>
                                                                        <TextField
                                                                            fullWidth
                                                                            id={emailId}
                                                                            name={emailId}
                                                                            value={p.emailId.toLowerCase()}
                                                                            error={Boolean(touchedEmailId && errorEmailId)}
                                                                            onChange={handleChange}
                                                                            onBlur={handleBlur}
                                                                            InputProps={{
                                                                                startAdornment: (
                                                                                    <InputAdornment position='start'>
                                                                                        <MailOutlineIcon className="svgIcons" />
                                                                                    </InputAdornment>
                                                                                ),
                                                                            }}
                                                                        />
                                                                    </Stack>
                                                                    <Stack style={{ width: isMobile ? '100%' : '328px' }}>
                                                                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Select Role</Typography>
                                                                        <TextField
                                                                            fullWidth
                                                                            id={rolePermissionId}
                                                                            name={rolePermissionId}
                                                                            value={p.rolePermissionId}
                                                                            error={Boolean(touchedrolePermissionId && errorRolePermissionId)}
                                                                            onChange={handleChange}
                                                                            onBlur={handleBlur}
                                                                            select
                                                                            InputProps={{
                                                                                startAdornment: (
                                                                                    <InputAdornment position='start'>
                                                                                        <GroupOutlinedIcon className="svgIcons" />
                                                                                    </InputAdornment>
                                                                                ),
                                                                            }}
                                                                        >
                                                                            {rolesList.map((item) => (
                                                                                <MenuItem fullWidth key={item.roleId} value={item.roleId}> {item.roleName} </MenuItem>
                                                                            ))}
                                                                        </TextField>
                                                                    </Stack>
                                                                </Stack>
                                                            </Stack>
                                                        </Stack>
                                                    )
                                                })}
                                            </div>
                                            {/* {errors &&
                                                errors.userArr && (
                                                    <span className="text-danger d-block">
                                                        hello
                                                    </span>
                                                )
                                            } */}
                                            <div style={{ textAlign: 'center' }}>
                                                <Button sx={buttonStyle3} onClick={() => push({
                                                    emailId: '',
                                                    rolePermissionId: '',
                                                    createdBy: 'kumar'
                                                })} variant="contained" endIcon={<AddIcon />}>
                                                    ADD
                                                </Button>
                                            </div>
                                        </>

                                    )}
                                </FieldArray>
                                <DialogActions style={{
                                    height: '66px',
                                    justifyContent: 'flex-end',
                                    padding: '0',
                                    marginTop: '20px'
                                }}>
                                    <Button sx={dialogButtonCancel} variant='outlined' onClick={handleCloseDialog}>Cancel</Button>
                                    <Button sx={dialogButtonStyle} style={{ marginLeft: '16px' }} disabled={!isValid} type='submit' variant="contained">Invite</Button>
                                </DialogActions>
                            </Form>
                        )}
                    </Formik>
            }


        </Dialog>
    )
}

