import './ResetPassword.scss';
import React, { useState } from 'react';
import * as yup from 'yup';
import { useFormik } from 'formik';
import { TextField, Button, Stack, InputAdornment, Typography, useMediaQuery } from '@mui/material';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import dizLogo from '../../../assets/images/dizLogo.svg'
import dizlogoOrigin from '../../../assets/images/dizlogoOrigin.svg'
import registerLeft from '../../../assets/images/registerLeft.svg'
import registerRight from '../../../assets/images/registerRight.svg';
import { useNavigate } from 'react-router-dom';
import { appapi } from '../../../api/apiClient';
import { showmessage } from '../../../utils/toastr';
import { buttonStyle1, buttonStyle2 } from '../../../utils/commonStyle';

/**
 * Validation Schema for register
 */
const validationSchema = yup.object({
    email: yup
        .string('Enter your Email')
        .matches(/^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/, 'Email is not Valid')
        .required('Email is Required'),
})

export default function ResetPassword() {
    const navigate = useNavigate();
    const isMobile = useMediaQuery('(max-width:1000px)');

    const [data, setData] = useState({
        email: '',
    })
    const [formSubmitted, SetFormSubmitted] = useState(false)

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: data,
        validationSchema: validationSchema,
        validateOnBlur: true,
        validateOnChange: true,
        onSubmit: (values) => {
            setData(values)
            resetPassword(values)
        },
    })

    const { handleSubmit, values, handleChange, errors, touched, handleBlur, isValid, dirty } = formik;

    /**
     * 
     * @param {email} obj 
     * This submit is for reseting password
     */
    const resetPassword = async (obj) => {
        let params = {
            params: {
                emailId: obj.email ? obj.email : data.email
            }
        }
        const response = await appapi.post(`/auth/forgot-password`, {}, params);
        if (response.ok) {
            const data = response.data;
            if (data.status === 'success') {
                showmessage(data.message)
                SetFormSubmitted(true)
            } else {
                showmessage(data.message, 5000, 'error')
            }
        } else {
            showmessage(`Something Went Wrong!`, 5000, 'error')
        }
    }

    /**
     * 
     * @returns Redirect to initial page which is login
     */
    const onClickLoginRoute = () => navigate(`/`)

    return (
        <div className='resetContainer'>
            <div className='leftBottomImg'>
                <img src={registerLeft} alt='Do It Zero Logo' />
            </div>
            <div className='rightBottomImg'>
                <img src={registerRight} alt='Do It Zero Logo' />
            </div>
            <div className='mainContainer'>
                <div className='logoImg'>
                    {isMobile ? <Stack className='mobileLogo' direction='row'>
                        <img src={dizlogoOrigin} alt='Do It Zero Logo' />
                        <Typography className='ff-Roboto fw-medium' style={{ marginLeft: '16px', display: 'flex', flexDirection: 'column', lineHeight: '20px' }}>
                            <span>Enterprise</span>
                            <span>Platform</span>
                        </Typography>
                    </Stack>
                        : <img src={dizLogo} alt='Do It Zero Logo' />
                    }
                </div>
                <div className='registerForm'>
                    <div className='innerDiv'>
                        {
                            formSubmitted ?
                                <div>
                                    <Stack>
                                        <Typography className='fw-bold ff-Roboto createHeading' color={'#3787C9'}> Successful! </Typography>
                                        <Typography className='fs-12 ff-Roboto fw-regular black' style={{ marginBottom: '6px' }}>
                                            Your request has been submitted successfully. Please check your email for further instructions.
                                        </Typography>
                                        <Typography className='ff-OpenSans fs-12 fw-regular'>Click here to <span className='fw-bold' onClick={resetPassword} style={{ color: '#8AB756', cursor: 'pointer' }}>Resend</span> mail </Typography>
                                        <Button sx={buttonStyle2} onClick={onClickLoginRoute} type='submit'>Go Back To Login</Button>
                                    </Stack>
                                </div>
                                :
                                <div>
                                    <Typography className='fw-bold ff-Roboto createHeading' color={'#3787C9'}> Reset Password </Typography>
                                    <Typography className='fs-12 ff-Roboto fw-regular black detailText'>
                                        Enter your registered email id to receive your password reset instructions.
                                    </Typography>
                                    <form onSubmit={handleSubmit}>
                                        <Stack>
                                            <Stack>
                                                <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Email</Typography>
                                                <TextField
                                                    fullWidth
                                                    id='email'
                                                    name='email'
                                                    value={values.email.toLowerCase() || ''}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    error={touched.email && Boolean(errors.email)}
                                                    InputProps={{
                                                        startAdornment: (
                                                            <InputAdornment position='start'>
                                                                <MailOutlineIcon className="svgIcons" />
                                                            </InputAdornment>
                                                        )
                                                    }}
                                                />
                                            </Stack>
                                            <Button sx={buttonStyle1} disabled={!(dirty && isValid)} type='submit'>Send</Button>
                                            <Typography className='ff-OpenSans fs-12 fw-regular' style={{ marginBottom: '96px', textAlign: 'center' }}>Go back to <span className='fw-bold' onClick={onClickLoginRoute} style={{ color: '#8AB756', cursor: 'pointer' }}>Login</span> </Typography>
                                        </Stack>
                                    </form>
                                </div>
                        }
                        <Typography className='fs-10 ff-Roboto fw-medium black dizcopyrights'>© 2022 Do it Zero Inc.</Typography>
                    </div>
                </div>
            </div>
        </div>
    )
}
