import '../UserManagement.scss';
import React from 'react';
import { Dialog, Slide, Stack, Typography, IconButton, useMediaQuery } from '@mui/material';
import ClearIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
import PersonIcon from '@mui/icons-material/Person';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import GroupOutlinedIcon from '@mui/icons-material/GroupOutlined';
import DomainIcon from '@mui/icons-material/Domain';
import accountTie from '../../../../assets/images/accountTie.svg';
import FmdGoodOutlinedIcon from '@mui/icons-material/FmdGoodOutlined';
import accountCircle from '../../../../assets/images/accountCircle.svg';

//Transtion of dialog from downwards to up
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});



export default function ViewUserDialog({ open, onHandleUserClose, user, onHandleEditDialog }) {

  const isMobile = useMediaQuery('(max-width:1000px)');

  /**
   * dialog style
   */
  const sx = {
    "& .MuiDialog-container": {
      alignItems: "end",
    },
    "& .MuiPaper-root": {
      width: '376px',
      minHeight: '424px',
      margin: isMobile ? '0' : '8px',
      padding: '23px 16px',
      borderRadius: isMobile ? '16px 16px 0 0' : '4px',
    }
  };

  const IconContainer = ({ children, backgroundColor }) => {
    return (
      <div className='iconBox flex-center' style={{ background: backgroundColor, borderRadius: '4px', width: '32px', height: '32px' }}>
        {children}
      </div>
    )
  }

  return (
    <Dialog
      sx={sx}
      open={open}
      onClose={onHandleUserClose}
      TransitionComponent={Transition}
      className='AddUserContainer'>
      <Stack style={{ borderBottom: '1px solid rgb(112,112,112,0.3)', paddingBottom: '10px', marginBottom: '16px' }} direction='row' justifyContent='space-between' alignItems='center' >
        <div className='flex'>
          <PersonIcon style={{ color: '#8AB756' }} />
          <Typography style={{ color: '#445C37', marginLeft: '16px' }} className='fw-bold fs-14 ff-Seigo' >USER DETAILS</Typography>
        </div>
        <div>
          <IconButton onClick={() => onHandleEditDialog(user)}>
            <EditIcon className='svgTransform' style={{ color: '#445C37' }} />
          </IconButton>
          <IconButton onClick={onHandleUserClose}>
            <ClearIcon style={{ color: '#445C37', transform: 'scale(1.143)' }} />
          </IconButton>
        </div>

      </Stack>
      <Stack>
        <Stack direction='row' style={{ marginBottom: '16px' }}>
          <IconContainer backgroundColor='#445C37'>
            <MailOutlineIcon style={{ color: '#E7EEDE', transform: 'scale(0.8)' }} />
          </IconContainer>
          <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
            <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Email</Typography>
            <Typography style={{ color: '#474747' }} className='ff-Seigo fs-14 fw-regular'>{user?.emailId}</Typography>
          </Stack>
        </Stack>
        <Stack direction='row' style={{ marginBottom: '16px' }}>
          <IconContainer backgroundColor='#445C37'>
            <GroupOutlinedIcon style={{ color: '#E7EEDE', transform: 'scale(0.8)' }} />
          </IconContainer>
          <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
            <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Roles</Typography>
            <Typography style={{ color: '#474747' }} className='ff-Seigo fs-14 fw-regular'>{user?.roleName || '-'}</Typography>
          </Stack>
        </Stack>
        <Stack direction='row' style={{ marginBottom: '16px' }}>
          <IconContainer backgroundColor='#E7EEDE'>
            <PersonOutlineIcon style={{ color: '#445C37' }} />
          </IconContainer>
          <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
            <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Name</Typography>
            <Typography style={{ color: '#474747' }} className='ff-Seigo fs-14 fw-regular'>{user?.name || '-'}</Typography>
          </Stack>
        </Stack>
        <Stack direction='row' style={{ marginBottom: '16px' }}>
          <IconContainer backgroundColor='#E7EEDE'>
            <img src={accountTie} alt='accountTie' />
          </IconContainer>
          <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
            <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Designation</Typography>
            <Typography style={{ color: '#474747' }} className='ff-Seigo fs-14 fw-regular'>{user?.designation || '-'}</Typography>
          </Stack>
        </Stack>
        <Stack direction='row' style={{ marginBottom: '16px' }}>
          <IconContainer backgroundColor='#E7EEDE'>
            <DomainIcon style={{ color: '#445C37', transform: 'scale(0.8)' }} />
          </IconContainer>
          <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
            <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Department</Typography>
            <Typography style={{ color: '#474747' }} className='ff-Seigo fs-14 fw-regular'>{user?.department || '-'}</Typography>
          </Stack>
        </Stack>
        <Stack direction='row' style={{ marginBottom: '16px' }}>
          <IconContainer backgroundColor='#E7EEDE'>
            <FmdGoodOutlinedIcon style={{ color: '#445C37', transform: 'scale(0.8)' }} />
          </IconContainer>
          <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
            <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Location</Typography>
            <Typography style={{ color: '#474747' }} className='ff-Seigo fs-14 fw-regular'>{user?.cityName || '-'}</Typography>
          </Stack>
        </Stack>
        <Stack direction='row'>
          <IconContainer backgroundColor={user?.activateStatus === 'active' ? '#E7EEDE' : '#E4E4E4'}>
            <img src={accountCircle} alt='accountTie' />
          </IconContainer>
          <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
            <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Status</Typography>
            <Typography style={{ color: '#474747' }} className='ff-Seigo fs-14 fw-regular'>{user?.activateStatus || '-'}</Typography>
          </Stack>
        </Stack>
      </Stack>
    </Dialog>
  )
}
