import '../UserManagement.scss';
import React, { useState } from 'react';
import { Stack, Typography, Box, IconButton, Menu, MenuItem } from '@mui/material';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import GroupOutlinedIcon from '@mui/icons-material/GroupOutlined';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import PersonIcon from '@mui/icons-material/Person';
import PersonOffIcon from '@mui/icons-material/PersonOff';
import DeleteIcon from '@mui/icons-material/Delete';
import { activateStatusType } from '../../../../utils/commonUtils';
import CheckCircleRoundedIcon from '@mui/icons-material/CheckCircleRounded';

export default function UserCard({ user, handleViewDetial, handleStatuChangeUser, handleDelete, selectedUser }) {

    //menu
    const [anchorEl, setAnchorEl] = useState(false);
    const open = Boolean(anchorEl)

    const handleMenuOpen = (event) => {
        setAnchorEl(event.currentTarget)
    }

    const handleMenuClose = () => {
        setAnchorEl(null)
    }

    return (
        <Box className='userCard'>
            <Stack direction='row' alignItems='flex-start' justifyContent='space-between'>
                <Stack>
                    <Stack direction='row'>
                        <MailOutlineIcon style={{ color: '#445C37', transform: 'scale(0.8)' }} />
                        <Stack style={{ marginLeft: '10px' }}>
                            <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Email</Typography>
                            <Typography style={{ color: '#000000' }} className='ff-Seigo fs-14 fw-regular'> {user?.emailId} </Typography>
                        </Stack>
                    </Stack>
                    <Stack direction='row' style={{ marginTop: '20px' }}>
                        <GroupOutlinedIcon style={{ color: '#445C37', transform: 'scale(0.8)' }} />
                        <Stack style={{ marginLeft: '10px' }}>
                            <Typography style={{ color: '#445C37' }} className='ff-Seigo fs-10 fw-semibold'>Roles</Typography>
                            <Typography style={{ color: '#000000' }} className='ff-Seigo fs-14 fw-regular'> {user?.roleName || '-'} </Typography>
                        </Stack>
                    </Stack>
                </Stack>
                {
                    user.isSelected
                        ?
                        <CheckCircleRoundedIcon style={{ color: '#8AB756' }} />
                        :
                        <>
                            <IconButton
                                id='resource-button'
                                onClick={handleMenuOpen}
                                aria-controls={open ? 'resource-menu' : undefined}
                                aria-haspopup='true'
                                aria-expanded={open ? 'true' : undefined}
                                style={{ padding: '0' }}
                            >
                                <MoreVertIcon style={{ color: '#474747' }} />
                            </IconButton>
                            <Menu PaperProps={{
                                sx: {
                                    width: '128px',
                                    "& .MuiList-root": {
                                        padding: '0'
                                    }
                                },
                            }}
                                className='appbar' id='resource-menu' anchorEl={anchorEl} open={open}
                                MenuListProps={{
                                    "aria-labelledby": 'resource-button',
                                }}
                                onClose={handleMenuClose}
                            >
                                <MenuItem disabled={user.activateStatus === activateStatusType.invite} className='ff-Roboto fs-12' onClick={() => handleStatuChangeUser(user)} onClose={handleMenuClose}>
                                    <Stack direction='row' alignItems='center'>
                                        {
                                            user.activateStatus === activateStatusType.active ?
                                                <PersonOffIcon style={{ color: '#8AB756' }} /> :
                                                <PersonIcon style={{ color: '#8AB756' }} />
                                        }
                                        <Typography className='ff-Seigo fs-12 fw-semibold' style={{ paddingLeft: '16px' }}>
                                            {

                                                user.activateStatus === activateStatusType.active ?
                                                    'Deactivate' :
                                                    'Activate'
                                            }
                                        </Typography>
                                    </Stack>
                                </MenuItem>
                                <MenuItem className='ff-Roboto fs-12' onClick={() => handleDelete(user)} onClose={handleMenuClose}>
                                    <Stack direction='row' alignItems='center'>
                                        <DeleteIcon style={{ color: '#8AB756' }} />
                                        <Typography className='ff-Seigo fs-12 fw-semibold' style={{ paddingLeft: '16px' }}>
                                            Delete
                                        </Typography>
                                    </Stack>
                                </MenuItem>
                            </Menu>
                        </>
                }
            </Stack>
            <Stack direction='row' alignItems='center' justifyContent='space-between' style={{ marginTop: '16px' }}>
                <Box className='flex-center' style={{ background: '#8AB756', borderRadius: '16px', height: '24px', minWidth: '64px' }}>
                    <Typography className='ff-Roboto fs-10 fw-medium' style={{ color: '#E9FCD3' }}>
                        {user.activateStatus === activateStatusType.active ? 'Active' : user.activateStatus === activateStatusType.deActivated ? 'Deactivated' : 'Invited'}
                    </Typography>
                </Box>
                <Typography className='ff-Seigo fs-12 fw-semibold' onClick={() => handleViewDetial(user)} style={{ color: '#445C37' }}>View Details</Typography>
            </Stack>
        </Box >
    )
}
