import './Register.scss';
import React, { useState, } from 'react';
import * as yup from 'yup';
import { useFormik } from 'formik';
import { TextField, Button, Stack, InputAdornment, IconButton, useMediaQuery, Typography, FormControlLabel, Checkbox } from '@mui/material';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import DomainIcon from '@mui/icons-material/Domain';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import dizLogo from '../../../assets/images/dizLogo.svg'
import dizlogoOrigin from '../../../assets/images/dizlogoOrigin.svg'
import registerLeft from '../../../assets/images/registerLeft.svg'
import registerRight from '../../../assets/images/registerRight.svg'
import { appapi } from '../../../api/apiClient';
import { showmessage } from '../../../utils/toastr';
import { useDispatch } from 'react-redux';
import { login } from '../../../redux/features/authSlice';
import { useParams } from 'react-router-dom'
import { buttonStyle1 } from '../../../utils/commonStyle';

/**
 * Validation Schema for register
 */
const validationSchema = yup.object({
  email: yup
    .string('Enter your Email')
    .matches(/^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/, 'Email is not Valid')
    .required('Email is Required'),
  name: yup
    .string('Enter your Name')
    .min(3, 'Name must minimum of 3 characters')
    .max(100, 'Name must maximum of 100 characters')
    .required('Name is Required')
    .trim(),
  department: yup
    .string('Enter your Department')
    .required('Deparment is Required')
    .max(50, 'Name must maximum of 50 characters')
    .trim(),
  password: yup
    .string('Enter your Password')
    .min(8, 'Password must be minimum of 6 characters')
    .max(12, 'Password must be maximum of 12 characters')
    // .matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,12}$/, 'Enter a password of minimum 8 characters with a combination of capital letters, small letters, symbols, and numbers.')
    .required('Password is requried')
    .trim(),
  termsandCondition: yup.bool().oneOf([true], 'Accept Terms & Conditions is required')
})

export default function Register() {

  const dispatch = useDispatch()
  const paramsFromUrl = useParams()

  const isMobile = useMediaQuery('(max-width:1000px)');

  const [visibility, setVisibility] = useState(false);
  const [data, setData] = useState({
    email: paramsFromUrl.emailId.toLowerCase(),
    name: '',
    department: '',
    password: '',
    termsandCondition: false
  })

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: data,
    validationSchema: validationSchema,
    validateOnBlur: true,
    validateOnChange: true,
    onSubmit: (values) => {
      register(values)
    },
  })

  const { handleSubmit, values, handleChange, errors, touched, handleBlur, isValid, dirty } = formik;

  /**
   * Register User
   * @param {email,name,department, password} obj 
   */
  const register = async (obj) => {
    let params = {
      params: {
        verificationToken: paramsFromUrl.verificationToken
      }
    }
    let payload = {
      name: obj.name,
      password: obj.password,
      department: obj.department,
      deviceId: '123'
    }
    const response = await appapi.post(`/auth/sign-up`, payload, params)
    if (response.ok) {
      const data = response.data;
      if (data.status === 'success') {
        showmessage(data.message)
        localStorage.setItem('diz', JSON.stringify(data.data))
        appapi.setHeader('Authorization', `Bearer ${data.data.accessToken}`)
        dispatch(login(data.data))
      } else {
        showmessage(data.message, 5000, 'error')
      }
    } else {
      showmessage(`Something Went Wrong!`, 5000, 'error')
    }
  }

  return (
    <div className='registerComponent'>
      <div className='leftBottomImg'>
        <img src={registerLeft} alt='Do It Zero Logo' />
      </div>
      <div className='rightBottomImg'>
        <img src={registerRight} alt='Do It Zero Logo' />
      </div>
      <div className='mainContainer'>
        <div className='logoImg'>
          {isMobile ? <Stack className='mobileLogo' direction='row'>
            <img src={dizlogoOrigin} alt='Do It Zero Logo' />
            <Typography className='ff-Roboto fw-medium' style={{ marginLeft: '16px', display: 'flex', flexDirection: 'column', lineHeight: '20px' }}>
              <span>Enterprise</span>
              <span>Platform</span>
            </Typography>
          </Stack>
            : <img src={dizLogo} alt='Do It Zero Logo' />
          }
        </div>
        <div className='registerForm'>
          <div className='innerDiv'>
            <Typography className='fw-bold ff-Roboto createHeading' color={'#3787C9'}> Create Account </Typography>
            <form onSubmit={handleSubmit}>
              <Stack>
                <Stack>
                  <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Email</Typography>
                  <TextField
                    fullWidth
                    id='email'
                    name='email'
                    value={values.email.toLowerCase() || ''}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={touched.email && Boolean(errors.email)}
                    disabled={values.email ? true : false}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position='start'>
                          <MailOutlineIcon className="svgIcons" />
                        </InputAdornment>
                      ),
                      classes: {
                        disabled: 'disabled'
                      }
                    }}
                  />
                </Stack>
                <Stack className='mt-14'>
                  <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Name</Typography>
                  <TextField
                    fullWidth
                    id='name'
                    name='name'
                    value={values.name.replace(/^\s/g, '') || ''}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={touched.name && Boolean(errors.name)}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position='start'>
                          <PersonOutlineIcon className="svgIcons" />
                        </InputAdornment>
                      )
                    }}
                  />
                </Stack>
                <Stack className='mt-14'>
                  <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Department</Typography>
                  <TextField
                    fullWidth
                    id='department'
                    name='department'
                    value={values.department.replace(/^\s/g, '') || ''}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={touched.department && Boolean(errors.department)}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position='start'>
                          <DomainIcon className="svgIcons" />
                        </InputAdornment>
                      )
                    }}
                  />
                </Stack>
                <Stack className='mt-14'>
                  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Password</Typography>
                    {/* <Typography className='fs-14 ff-Roboto fw-regular mb-7' color={'#8C8C8C'}>Forgot Password?</Typography> */}
                  </div>
                  <TextField
                    fullWidth
                    id='password'
                    name='password'
                    value={values.password.trim() || ''}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    type={visibility ? 'text' : 'password'}
                    error={touched.password && Boolean(errors.password)}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position='start'>
                          <LockOutlinedIcon className="svgIcons" />
                        </InputAdornment>
                      ),
                      endAdornment: (
                        <InputAdornment position='end'>
                          {
                            <IconButton onClick={() => setVisibility(!visibility)}>
                              {visibility ? <VisibilityIcon /> : <VisibilityOffIcon />}
                            </IconButton>
                          }
                        </InputAdornment>
                      )
                    }}
                  />
                </Stack>
                <Stack className='mt-14'>
                  <FormControlLabel
                    // error={true}
                    required
                    control={
                      <Checkbox id='termsandCondition' name='termsandCondition' color="success" checked={!!values.termsandCondition} onChange={handleChange} />
                    }
                    label={<Typography className='fs-12 ff-Roboto fw-regular black'>I agree to the Terms of Service and Privacy Policy</Typography>}
                  />
                </Stack>
                <Button sx={buttonStyle1} disabled={!(dirty && isValid)} type='submit'>Create</Button>
              </Stack>
            </form>
            <Typography className='fs-10 ff-Roboto fw-medium black dizcopyrights'>© 2022 Do it Zero Inc.</Typography>
          </div>
        </div>
      </div>
    </div>
  )
}
