import '../UserManagement.scss';
import React, { useState, useEffect } from 'react';
import { Dialog, Slide, Stack, Typography, IconButton, TextField, InputAdornment, MenuItem, DialogActions, Button, useMediaQuery } from '@mui/material';
import ClearIcon from '@mui/icons-material/Clear';
import PersonIcon from '@mui/icons-material/Person';
import DeleteIcon from '@mui/icons-material/Delete';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import DomainIcon from '@mui/icons-material/Domain';
import GroupOutlinedIcon from '@mui/icons-material/GroupOutlined';
import accountTie from '../../../../assets/images/accountTie.svg';
import FmdGoodOutlinedIcon from '@mui/icons-material/FmdGoodOutlined';
import * as yup from 'yup';
import { useFormik } from 'formik';
import { dialogButtonStyle, dialogButtonCancel } from '../../../../utils/commonStyle';
import { useSelector } from 'react-redux';
import { appapi } from '../../../../api/apiClient';
import { showmessage } from '../../../../utils/toastr';
import { activateStatusType } from '../../../../utils/commonUtils';
import PersonOffIcon from '@mui/icons-material/PersonOff';

//Transtion of dialog from downwards to up
const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

/**
 * Validation Schema for register
 */
const validationSchema = yup.object({
    email: yup
        .string('Enter your Email')
        .email('Enter valid mail id')
        .required('Email is Required'),
    rolePermissionId: yup
        .string('Select your role')
        .required('Role is Required'),
    name: yup
        .string('Enter your Name')
        .min(3, 'Name must minimum of 3 characters')
        .required('Name is Required')
        .trim(),
    designation: yup
        .string('Enter your Department')
        .required('Designation is Required')
        .trim(),
    department: yup
        .string('Enter your Department')
        .required('Deparment is Required')
        .trim(),
    cityId: yup
        .string('Enter your Location')
        .required('Location is Required')
        .trim()
})

export default function EditUserDialog({ open, onHandleEditClose, user, enterpriseUser, onHandleDeleteUser, handleChangeStatus }) {

    const isMobile = useMediaQuery('(max-width:1000px)');

    /**
     * dialog style
     */
    const sx = {
        "& .MuiDialog-container": {
            alignItems: "end",
        },
        "& .MuiPaper-root": {
            width: '360px',
            minHeight: '424px',
            maxHeight: isMobile ? '620px' : '568px',
            margin: isMobile ? '0' : '8px',
            padding: '0 16px 23px 16px',
            borderRadius: '16px 16px 0 0',
        }
    };

    const { rolesList, citiesList } = useSelector(state => state.app);

    const [data, setData] = useState({
        email: '',
        rolePermissionId: '',
        name: '',
        designation: '',
        department: '',
        cityId: ''
    })

    useEffect(() => {
        setData({
            email: user?.emailId || '',
            rolePermissionId: user?.roleId || '',
            name: user?.name || '',
            designation: user?.designation || '',
            department: user?.department || '',
            cityId: user?.cityId
        })
    }, [user?.cityId, user?.department, user?.designation, user?.emailId, user?.name, user?.roleId]);

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: data,
        validationSchema: validationSchema,
        validateOnBlur: true,
        validateOnChange: true,
        onSubmit: (values) => {
            update(values)
        },
    })

    const { handleSubmit, values, handleChange, errors, touched, handleBlur, isValid } = formik;

    const update = async (obj) => {
        setData(obj)
        let payload = obj;
        delete payload.email;
        let params = {
            params: {
                enterpriseId: enterpriseUser.enterpriseId,
                enterpriseUserId: user.enterpriseUserId
            }
        }
        const response = await appapi.put(`/user/${enterpriseUser.enterpriseId}/${user.enterpriseUserId}`, payload, params)
        if (response.ok) {
            const data = response.data
            onHandleEditClose()
            if (data.status === 'success') {
                showmessage(`Edited Successfully!!`)
            } else {
                showmessage(data.message, 5000, 'error')
            }
        } else {
            showmessage(`Something Went Wrong!`, 5000, 'error')
        }
    }

    return (
        <Dialog
            sx={sx}
            open={open}
            onClose={onHandleEditClose}
            TransitionComponent={Transition}
            className='AddUserContainer'
        >
            <Stack style={{ paddingBottom: '10px', marginBottom: '16px', position: 'sticky', top: '0', paddingTop: '23px', zIndex: '99', background: '#FFF' }} direction='row' justifyContent='space-between' alignItems='center' >
                <div className='flex'>
                    <PersonIcon style={{ color: '#8AB756' }} />
                    <Typography style={{ color: '#445C37', marginLeft: '16px' }} className='fw-bold fs-14 ff-Seigo' >EDIT DETAILS</Typography>
                </div>
                <div>
                    <IconButton onClick={() => onHandleDeleteUser(user, 'fromEdit')}>
                        <DeleteIcon className='svgTransform' style={{ color: '#445C37' }} />
                    </IconButton>
                    {user?.activateStatus === 'invite' ? null :
                        <IconButton onClick={() => handleChangeStatus(user)}>
                            {
                                user?.activateStatus === activateStatusType.active ?
                                    <PersonOffIcon style={{ color: '#445C37' }} /> :
                                    <PersonIcon style={{ color: '#445C37' }} />
                            }
                        </IconButton>
                    }
                    <IconButton onClick={onHandleEditClose}>
                        <ClearIcon style={{ color: '#445C37', transform: 'scale(1.143)' }} />
                    </IconButton>
                </div>
            </Stack>
            <form onSubmit={handleSubmit} className='editContainer'>
                <Stack>
                    <Stack>
                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Email</Typography>
                        <TextField
                            fullWidth
                            id='email'
                            name='email'
                            value={values.email || ''}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            disabled={values.email ? true : false}
                            error={touched.email && Boolean(errors.email)}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position='start'>
                                        <MailOutlineIcon className="svgIcons" />
                                    </InputAdornment>
                                ),
                                classes: {
                                    disabled: 'disabled'
                                }
                            }}
                        />
                    </Stack>
                    <Stack className='mt-14'>
                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Select Role</Typography>
                        <TextField
                            fullWidth
                            id='rolePermissionId'
                            name='rolePermissionId'
                            value={values.rolePermissionId || ''}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            select
                            error={touched.rolePermissionId && Boolean(errors.rolePermissionId)}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position='start'>
                                        <GroupOutlinedIcon className="svgIcons" />
                                    </InputAdornment>
                                ),
                            }}
                        >
                            {rolesList.map((item) => (
                                <MenuItem fullWidth key={item.roleId} value={item.roleId}> {item.roleName} </MenuItem>
                            ))}
                        </TextField>
                    </Stack>
                    <Stack className='mt-14'>
                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Name</Typography>
                        <TextField
                            fullWidth
                            id='name'
                            name='name'
                            value={values.name.replace(/^\s/g, '') || ''}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.name && Boolean(errors.name)}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position='start'>
                                        <PersonOutlineIcon style={{ color: '#8AB756' }} />
                                    </InputAdornment>
                                )
                            }}
                        />
                    </Stack>
                    <Stack className='mt-14'>
                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Designation</Typography>
                        <TextField
                            fullWidth
                            id='designation'
                            name='designation'
                            value={values.designation.replace(/^\s/g, '') || ''}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.designation && Boolean(errors.designation)}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position='start'>
                                        <img src={accountTie} style={{ margin: '0 5px', filter: 'invert(63%) sepia(63%) saturate(337%) hue-rotate(45deg) brightness(95%) contrast(84%)' }} alt='accountTie' />
                                    </InputAdornment>
                                )
                            }}
                        />
                    </Stack>
                    <Stack className='mt-14'>
                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Department</Typography>
                        <TextField
                            fullWidth
                            id='department'
                            name='department'
                            value={values.department.replace(/^\s/g, '') || ''}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.department && Boolean(errors.department)}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position='start'>
                                        <DomainIcon className="svgIcons" />
                                    </InputAdornment>
                                )
                            }}
                        />
                    </Stack>
                    <Stack className='mt-14'>
                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Location</Typography>
                        <TextField
                            fullWidth
                            id='cityId'
                            name='cityId'
                            value={values.cityId || ''}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            select
                            error={touched.cityId && Boolean(errors.cityId)}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position='start'>
                                        <FmdGoodOutlinedIcon className="svgIcons" />
                                    </InputAdornment>
                                ),
                            }}
                        >
                            {citiesList.map((item) => (
                                <MenuItem fullWidth key={item.cityId} value={item.cityId}> {item.cityName} </MenuItem>
                            ))}
                        </TextField>
                    </Stack>
                    <DialogActions style={{
                        height: '66px',
                        justifyContent: 'space-between',
                        padding: '0',
                        marginTop: '20px'
                    }}>
                        <Button sx={dialogButtonCancel} variant='outlined' onClick={onHandleEditClose}>Cancel</Button>
                        <Button sx={dialogButtonStyle} disabled={!isValid} type='submit' variant="contained">Update</Button>
                    </DialogActions>
                </Stack>
            </form>
        </Dialog>
    )
}
