import './ResetConfirm.scss';
import React, { useState } from 'react';
import * as yup from 'yup';
import { useFormik } from 'formik';
import { TextField, Button, Stack, InputAdornment, IconButton, Typography, useMediaQuery } from '@mui/material';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import dizLogo from '../../../assets/images/dizLogo.svg'
import dizlogoOrigin from '../../../assets/images/dizlogoOrigin.svg'
import registerLeft from '../../../assets/images/registerLeft.svg'
import registerRight from '../../../assets/images/registerRight.svg';
import { appapi } from '../../../api/apiClient';
import { showmessage } from '../../../utils/toastr';
import { useParams, useNavigate } from 'react-router-dom'
import { buttonStyle1 } from '../../../utils/commonStyle';

/**
 * Validation Schema for register
 */
const validationSchema = yup.object({
    password: yup
        .string('Enter your Password')
        .min(8, 'Password must be minimum of 6 characters')
        .max(12, 'Password must be maximum of 12 characters')
        .required('Password is requried')
        .trim(),
    reConfirmPassword: yup
        .string()
        .oneOf([yup.ref('password'), null], 'Passwords must match')
        .required('Password is requried')
        .trim(),
})

export default function ResetConfirm() {

    const paramsFromUrl = useParams()
    const navigate = useNavigate()

    const isMobile = useMediaQuery('(max-width:1000px)');

    const [visibility1, setVisibility1] = useState(false);
    const [visibility2, setVisibility2] = useState(false);
    const [data, setData] = useState({
        password: '',
        reConfirmPassword: ''
    })

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: data,
        validationSchema: validationSchema,
        validateOnBlur: true,
        validateOnChange: true,
        onSubmit: (values) => {
            reset(values)
        },
    })

    const { handleSubmit, values, handleChange, errors, touched, handleBlur, isValid, dirty } = formik;

    /**
     * New password creation
     * @param {password, reconfirmPassword} obj 
     */
    const reset = async (obj) => {
        let params = {
            params: {
                verificationToken: paramsFromUrl.verificationToken
            }
        }
        let payload = {
            password: obj.password,
        }
        const response = await appapi.post(`/auth/reset-password`, payload, params)
        if (response.ok) {
            const data = response.data;
            if (data.status === 'success') {
                showmessage(data.message)
                navigate('/')
            } else {
                showmessage(data.message, 5000, 'error')
            }
        } else {
            showmessage(`Something Went Wrong!`, 5000, 'error')
        }
    }

    return (
        <div className='resetConfirmContainer'>
            <div className='leftBottomImg'>
                <img src={registerLeft} alt='Do It Zero Logo' />
            </div>
            <div className='rightBottomImg'>
                <img src={registerRight} alt='Do It Zero Logo' />
            </div>
            <div className='mainContainer'>
                <div className='logoImg'>
                    {isMobile ? <Stack className='mobileLogo' direction='row'>
                        <img src={dizlogoOrigin} alt='Do It Zero Logo' />
                        <Typography className='ff-Roboto fw-medium' style={{ marginLeft: '16px', display: 'flex', flexDirection: 'column', lineHeight: '20px' }}>
                            <span>Enterprise</span>
                            <span>Platform</span>
                        </Typography>
                    </Stack>
                        : <img src={dizLogo} alt='Do It Zero Logo' />
                    }
                </div>
                <div className='registerForm'>
                    <div className='innerDiv'>
                        <Typography className='fw-bold ff-Roboto createHeading' color={'#3787C9'}> Reset your Password </Typography>
                        <Typography className='fs-12 ff-Roboto fw-regular black detailText'>Enter a password of minimum 8 characters with a combination of capital letters, small letters, symbols, and numbers.</Typography>
                        <form onSubmit={handleSubmit}>
                            <Stack>
                                <Stack>
                                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Password</Typography>
                                    </div>
                                    <TextField
                                        fullWidth
                                        id='password'
                                        name='password'
                                        value={values.password.trim() || ''}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        type={visibility1 ? 'text' : 'password'}
                                        error={touched.password && Boolean(errors.password)}
                                        InputProps={{
                                            startAdornment: (
                                                <InputAdornment position='start'>
                                                    <LockOutlinedIcon className="svgIcons" />
                                                </InputAdornment>
                                            ),
                                            endAdornment: (
                                                <InputAdornment position='end'>
                                                    {
                                                        <IconButton onClick={() => setVisibility1(!visibility1)}>
                                                            {visibility1 ? <VisibilityIcon /> : <VisibilityOffIcon />}
                                                        </IconButton>
                                                    }
                                                </InputAdornment>
                                            )
                                        }}
                                    />
                                </Stack>
                                <Stack className='mt-14'>
                                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                        <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={'#445C37'}>Retype Password</Typography>
                                    </div>
                                    <TextField
                                        fullWidth
                                        id='reConfirmPassword'
                                        name='reConfirmPassword'
                                        value={values.reConfirmPassword.trim() || ''}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        type={visibility2 ? 'text' : 'password'}
                                        error={touched.reConfirmPassword && Boolean(errors.reConfirmPassword)}
                                        InputProps={{
                                            startAdornment: (
                                                <InputAdornment position='start'>
                                                    <LockOutlinedIcon className="svgIcons" />
                                                </InputAdornment>
                                            ),
                                            endAdornment: (
                                                <InputAdornment position='end'>
                                                    {
                                                        <IconButton onClick={() => setVisibility2(!visibility2)}>
                                                            {visibility2 ? <VisibilityIcon /> : <VisibilityOffIcon />}
                                                        </IconButton>
                                                    }
                                                </InputAdornment>
                                            )
                                        }}
                                    />
                                </Stack>
                                <Button sx={buttonStyle1} disabled={!(dirty && isValid)} type='submit'>Reset</Button>
                            </Stack>
                        </form>
                        <Typography className='fs-10 ff-Roboto fw-medium black dizcopyrights'>© 2022 Do it Zero Inc.</Typography>
                    </div>
                </div>
            </div>
        </div>
    )
}
